export interface MinimalHttpError {
  /** errno symbolic name** like `ENETUNREACH`, Not http status code. */
  code?: string | undefined;
  response?: {
    data: {};
    status?: number;
    statusText?: string;
    headers?: Record<string, any>;
  };
  config?: {
    url?: string;
    method?: string;
    entityFqdn?: string;
    methodFqn?: string;
    artifactId?: string;
  };
  requestId: string | undefined;
}
